<template>
  <div class="xgMobilewrap">
    <div class="xgMobile">
      <div class="mobile">
        <span>新密码</span>
        <input type="password" v-model="password" placeholder="输入新密码">
      </div>
      <div class="mobile">
        <span>再次输入</span>
        <input type="password"  v-model="repassword" placeholder="再次输入新密码">
      </div>
    </div>
    <van-button type="default btn_cz" @click="toChange">确认</van-button>
    <div class="bottom_pass">
      <span @click="()=>{this.$router.go(-1)}" class="iconfont icon-shangyiye" ></span>
      <span :class="activeNum>=3?'':'disabled'" @click="()=>{if(activeNum>=3){this.$router.go(1)}}" class="iconfont icon-xiayiye" ></span>
    </div>
  </div>
</template>

<script>
  import {apiPost} from "@/common/js/api";
  import { Toast } from 'vant';
  import { getStore,setStore } from '../assets/utils';
    export default {
        name: "xgDengpassword",
      data(){
        return{
          password:'',
          repassword:'',
          accessToken:'',
          activeNum:''
        }
      },
      mounted(){
          this.accessToken=this.$cookie.get('accessToken');
        this.activeNum = getStore('activeNum')
      },
      methods:{
        toChange(){
          let psdreg=/^[\w]{6,12}$/;
          if(!psdreg.test(this.password)){
            Toast('请输入正确格式的密码!')
          }else if(this.password != this.repassword ){
            Toast('两次密码不一致!')
          }else{
            apiPost('/account/password-save',{accessToken:this.accessToken,pwd_new1:this.password,pwd_new2:this.repassword}).then((res)=>{
              console.log(res)
              if(res.status==200){
                Toast(res.data.message)
                setStore('activeNum',3)
                this.$router.go(-2)
              }else {
                Toast('请重新输入!')
              }
            })
          }
        }
      }
    }
</script>

<style scoped lang="scss">
  .xgMobilewrap{
    width: 100%;
    height: 100%;
    background: #E6E9ED;
    padding-top: 0.18rem;
    .xgMobile{
      width:3.5rem;
      height:1.12rem;
      background:rgba(255,255,255,1);
      border-radius:0.04rem;
      margin: 0 auto;
      .mobile{
        height: 0.56rem;
        width: 3.5rem;
        line-height: 0.56rem;
        span{
          font-size: 0.17rem;
          color: #333;
          font-weight: 400;
          width: 24%;
          float: left;
          text-align: center;
        }
        input{
          font-size: 0.15rem;
          width: 70%;
          height: 0.5rem;
          border: none;
          margin-left: 0.15rem;
          color: #9a9a9a;
        }
      }

    }
    .btn_cz{
      width:3.06rem;
      height:0.47rem;
      background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
      border-radius:0.04rem;
      color: white;
      display: block;
      font-size: 0.19rem;
      margin:0.16rem auto 0;
      margin-top: 1.5rem;
    }
  }
</style>



